
import produce from "immer"

//仓库初始化的数据
const initState={
    // size数据持久化
    size:localStorage.getItem('size')||'middle'
}

export default (state=initState,{type,payload})=>{

    return produce(state,newState=>{
        switch (type) {
            case 'APP_SIZE_ADD':
                newState.size+=payload
                break;
            case 'APP_SIZE':
                newState.size=payload;
                localStorage.setItem('size',payload)
                break;
        }
    })
}