
import { Breadcrumb, Col, Row } from 'antd';
import useBreadcrumb from '@/hooks/useBreadcrumb'
import { useNavigate } from 'react-router-dom'
import {
    FullscreenOutlined,
    FontSizeOutlined,
    FontColorsOutlined,
    FullscreenExitOutlined
} from '@ant-design/icons'
import screenfull from 'screenfull';
import { useState } from 'react'
import { Dropdown, Menu } from 'antd';

import { useDispatch, useSelector } from 'react-redux'

//size按钮下拉框需要的数据
const sizes = ['large', 'middle', 'small'];


export default () => {
    // 判断是否全屏的状态
    const [full, setFull] = useState(false);
    const breads = useBreadcrumb();  //面包屑
    let navigate = useNavigate();//跳转路由的方法
    const { size } = useSelector(state => state.app);
    const dispatch = useDispatch()
    //面包屑路由跳转
    let skipTo = (route, idx) => {
        //有path 并且最后一个不能跳
        if (route.path && idx != breads.length - 1)
            navigate(route.path, { replace: true })
    }
    //条件函数
    let isSkip = (route, idx) => {
        return (route.path && idx !== breads.length - 1)
    }

    // 字体大小改变
    const toggleSize = (size) => {
        dispatch({type:'APP_SIZE',payload:size})
    }

    //渲染面包屑函数
    const renderBreads = () => {
        return breads.map((ele, idx) => (
            <Breadcrumb.Item key={ele.key} onClick={() => skipTo(ele, idx)}>
                <span style={{
                    color: isSkip(ele, idx) ? '#fff' : 'rgb(192,188,188)',
                    cursor: isSkip(ele, idx) ? 'pointer' : 'default'
                }}>
                    {ele.label}
                </span>
            </Breadcrumb.Item>
        ))
    }

    // 全屏与退出全屏
    let screenFull = () => {
        if (screenfull.isEnabled) {
            screenfull.toggle();
        }
    }
    //全屏change事件
    screenfull.onchange(() => {
        setFull(screenfull.isFullscreen)
    })

    return (
        <div className='my-header'>
            <Row>
                <Col span={18}>
                    <Breadcrumb>
                        {
                            renderBreads()
                        }
                    </Breadcrumb>
                </Col>
                <Col span={3} offset={3}>
                    <div className='icons'>
                        {
                            full ?
                                <FullscreenExitOutlined onClick={screenFull} /> :
                                <FullscreenOutlined onClick={screenFull} />
                        }
                        <Dropdown
                            overlay={
                                <Menu
                                    items={
                                        sizes.map(ele => {
                                            return {
                                                key: ele,
                                                label: <div className={size==ele?'on':''} onClick={() => toggleSize(ele)}>{ele}</div>
                                            }
                                        })
                                    }
                                />
                            }
                            placement='bottomRight'
                            trigger={['click']}
                            arrow={{ pointAtCenter: true }}
                        >
                            <FontSizeOutlined />
                        </Dropdown>
                        <FontColorsOutlined />
                    </div>
                </Col>
            </Row>
        </div>
    )
}